<script>
export default {
    data() {
        return {
            width: 460,
            height: 300,
            current: 0,
            imageSources: [
                "https://img.siksinhot.com/place/1590631878301943.jpeg?w=307&h=300&c=Y",
                "https://img.siksinhot.com/place/1590631769965938.jpeg?w=307&h=300&c=Y",
                "https://img.siksinhot.com/place/1590631747313937.jpeg?w=307&h=300&c=Y",
                "https://img.siksinhot.com/place/1590631706035936.jpg?w=307&h=300&c=Y"
            ]
        };
    },
    mounted() {
        this.startSlideshow();
    },
    methods: {
        setCurrent(index) {
            this.current = index;
            this.moveTo();
        },
        startSlideshow() {
            setInterval(this.nextSlide, 3000);
        },
        moveTo() {
            this.$nextTick(() => {
                const images = this.$refs.images;
                if (images) {
                    images.style.left = `-${this.current * this.width}px`;
                }
            });
        },
        prevSlide() {
            this.current =
                (this.current - 1 + this.imageSources.length) % this.imageSources.length;
            this.moveTo();
        },
        nextSlide() {
            this.current = (this.current + 1) % this.imageSources.length;
            this.moveTo();
        }
    }
};
</script>
  
