<script>
export default {
    data() {
        return {
            width: 460,
            height: 300,
            current: 0,
            imageSources: [
                "https://img.siksinhot.com/place/1464572325374586.png?w=307&h=300&c=Y",
                "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQ4G5J_jeWknf6H7aEnwWklWaRFqJjpuijcfA&usqp=CAU",
                "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcToDcaT0ymgyYD_Q5uLZXzh9igytt4TEPnX9w&usqp=CAU",
                "https://img.siksinhot.com/story/1372132629563133.jpg?w=307&h=300&c=Y"
            ]
        };
    },
    mounted() {
        this.startSlideshow();
    },
    methods: {
        setCurrent(index) {
            this.current = index;
            this.moveTo();
        },
        startSlideshow() {
            setInterval(this.nextSlide, 3000);
        },
        moveTo() {
            this.$nextTick(() => {
                const images = this.$refs.images;
                if (images) {
                    images.style.left = `-${this.current * this.width}px`;
                }
            });
        },
        prevSlide() {
            this.current =
                (this.current - 1 + this.imageSources.length) % this.imageSources.length;
            this.moveTo();
        },
        nextSlide() {
            this.current = (this.current + 1) % this.imageSources.length;
            this.moveTo();
        }
    }
};
</script>
  
